import React from "react";
import "./kre_home.css";
import LeftNavBar from "./LeftNavBar";
import RightNavBar from "./RightNavBar";
import GuideLeftNavBar from "../pages/guides/GuideLeftNavBar"
import HomeMainBodyContent from "../pages/main/HomeMainBodyContent";
import { BrowserRouter as Switch, Route } from 'react-router-dom';
import GuideHubMainBody from "../pages/guides/GuideHubMainBody";
import HeroHub from "../pages/guides/HeroHub";

function MainBody() {
    return (
        <div className="row">
            <div className="col-2">
                <Route path="/KRE-Web-React/" exact component={LeftNavBar} />
                <Route path="/KRE-Web-React/guides" component={GuideLeftNavBar} />
            </div>
            
            <div className="col-8">
                <div className="centerContent body-panels">
                    <Route path="/KRE-Web-React/" exact component={HomeMainBodyContent} />
                    <Route path="/KRE-Web-React/guides" component={GuideHubMainBody} />
                    <Route path="/KRE-Web-React/guides/heroes/" component={HeroHub}/>
                </div>
            </div>

            <div className="col-2">
                <Route path="/KRE-Web-React/" component={RightNavBar} />
            </div>
        </div>
    );
}

export default MainBody;