import React from "react";
import "../../common_ui/kre_home.css";
import "../../common_ui/kre_guide_hub.css";
import GuideHubTabs from "./GuideHubTabs";
import SubDivider from "../../common_ui/SubDivider";
import { BrowserRouter as Switch, Route } from 'react-router-dom';
import HeroGuideHeroSelect from "./HeroGuideHeroSelect";

function GuideHubMainBody() {
    return(
        <div>
            <Route path='/KRE-Web-React/guides/beginners' exact component={GuideHubTabs}/>
            <Route path='/KRE-Web-React/guides/heroes' exact component={GuideHubTabs}/>
            <Route path='/KRE-Web-React/guides/misc' exact component={GuideHubTabs}/>
            
            <Route path='/KRE-Web-React/guides/beginners'
                render={(props) => <SubDivider text="Beginner Guides"/>}
            />

            <Route path='/KRE-Web-React/guides/heroes'
                render={(props) => <SubDivider text="Hero Guides"/>}
            />
            <Route path='/KRE-Web-React/guides/heroes' exact component={HeroGuideHeroSelect}/>

            <Route path='/KRE-Web-React/guides/misc'
                render={(props) => <SubDivider text="Miscellaneous Guides"/>}
            />
        </div>
    );
}

export default GuideHubMainBody;